.content {
  color: $primary-color;
  font-family: $mainFont;
  font-weight: 400;
  font-size: 15px;
  line-height: 140%;
  margin-bottom: 30px;

  @media (min-width: $xxl) {
    margin-bottom: 3.3rem;
    padding-top: 1.8rem;
  }

  &__slider {
    margin-bottom: 20px;
    @media (min-width: $xxl) {
      margin-bottom: 3.3rem;
    }
  }

  & .slider .swiper-slide {
    max-height: 300px;
    @media (min-width: $md) {
      min-height: 500px;
    }
  }

  & .slider .swiper-slide img {
    height: 100%;
    min-height: 295px;
    @media (min-width: $md) {
      min-height: 100%;
      min-height: 500px;
    }
  }

  & h1 {
    margin-bottom: 0.8rem;
    font-weight: 700;
    font-size: 27px;
    line-height: 130%;
    /* identical to box height, or 78px */

    letter-spacing: 0.02em;
    letter-spacing: 0.04em;
    text-transform: uppercase;

    color: rgba(73, 76, 79, 0.5);

    @include ff(
      'font-size',
      (
        375px: 27px,
        1280px: 60px,
      )
    );
  }

  & p {
    margin-bottom: 0;
  }
}
