:root {
  --mb:0.75rem;
  --pt: 30px;

  @media (min-width: $sm) {
    --pt: 1.4rem;
  }
}
.nav-menu {
  display: flex;
  flex-wrap: wrap;
  // align-items: center;
  justify-content: space-between;
  padding: 1.5rem 0 1.1rem;
  padding-top: var(--pt);
  padding-bottom: calc(var(--pt) - var(--mb));

  @media (max-width: $xs) {
    padding-left: 16px;
    padding-right: 16px;
  }

  @media (min-width: $sm) {
    padding-top: var(--pt);
    padding-bottom: calc(var(--pt) - var(--mb));
  }

  @media (min-width: $xl) {
    align-items: start;
  }

  &__list {
    display: flex;
    flex-wrap: wrap;

    @include listReset();

    @media (min-width: $md) {
      flex-wrap: nowrap;
      align-items: center;
    }
    @media (min-width: $lg) {
      flex-wrap: wrap;
    }
  }

  &__item {
    margin-bottom: var(--mb);

    &:not(:last-child) {
      margin-right: 0.65rem;

      @media (min-width: $xs) {
        margin-right: 0.55rem;
      }

      @media (min-width: $xl) {
        margin-right: 1rem;
      }
      @media (min-width: $xxl) {
        margin-right: 1.65rem;
      }
    }

    &:nth-child(2) {
      width: 100%;
      max-width: 260px;
      margin-right: 0;

      @media (min-width: 450px) {
        margin-right: 0.65rem;
      }

      @media (min-width: $xs) {
        width: auto;
      }

      @media (min-width: $xl) {
        margin-right: 1.75rem;
      }
    }

    @media (min-width: $lg) {
      margin-right: 28px;

      &--city {
        margin-right: 20px;
      }
    }
  }

  &__link {
    display: flex;
    flex-wrap: nowrap;
    align-items: center;
    padding: var(--nav-btn-pd);
    font-family: inherit;
    font-weight: 400;
    font-size: 14px;
    line-height: 140%;

    color: $primary-color;
    background-color: $blue-color;
    box-shadow: $boxShadow;
    border-radius: $borderRadius;
    transition: 1s;

    text-decoration: none;

    @include ff(
      'font-size',
      (
        375px: 14px,
        768px: 14px,
        1200px: 20px,
      )
    );

    @media (min-width: $xl) {
      line-height: 130%;
    }

    &:focus,
    &:hover {
      color: $primary-color;
      background-color: $yellow-color;
      text-decoration: none;

      & svg {
        fill: $primary-color;
        stroke: $white-color;
      }
    }

    & span {
      padding-right: 12px;
    }

    &--sale {
      & span {
        padding-right: 20px;
      }
    }

    &.active {
      background-color: $yellow-color;
      color: $primary-color;
    }

    @media (min-width: $lg) {
    }

    & svg {
      fill: $primary-color;
      stroke: $white-color;
      width: 20px;
      height: 20px;
    }
  }

  & .link-pay {
    margin-bottom: var(--mb);
  }
}
