.section-korus {
  z-index: 1;
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  max-width: 550px;
  min-height: 254px;
  padding: 1rem;
  padding-top: 5rem;
  background-color: $primary-color;

  // background-size: cover;

  transition: 1s;
  background-size: cover;

  text-decoration: none;
  overflow: hidden;

  &::before {
    content: '';
    position: absolute;
    z-index: -1;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: linear-gradient(
        209.04deg,
        rgba(0, 0, 0, 0) 55.12%,
        rgba(0, 0, 0, 0.7) 86.38%
      ),
      var(--pic);
    background-repeat: no-repeat;
    background-position: 50%;
    background-size: cover;

    animation: animBgRev 1s ease-in-out;
    transition: 1s;
  }

  &:focus,
  &:hover {
    transition: 1s;
    text-decoration: none;

    &::before {
      animation: animBg 1s forwards;
    }
  }

  & .title {
    @include px-to-rem(25, margin-bottom);
    color: $white-color;
  }

  & .link-text--more {
    margin-left: auto;
    margin-right: 0;
    color: $white-color;

    &:focus,
    &:hover {
      color: $yellow-color;
    }
  }
}


