.nav-city {
  position: relative;
  height: 100%;

  & .title {
    margin-bottom: 15px;
    font-family: $mainFontBold;
    font-weight: 700;
    font-size: 15px;
    line-height: 140%;
    text-transform: uppercase;
  }

  &__title {
    display: flex;
    flex-wrap: nowrap;
    align-items: center;
    @include buttonReset();

    padding: var(--nav-btn-pd);
    font-family: inherit;
    font-weight: 400;
    font-size: 14px;

    height: 100%;

    color: $primary-color;
    background-color: $blue-color;
    box-shadow: $boxShadow;
    border-radius: $borderRadius;
    transition: 1s;
    line-height: normal;

    text-decoration: none;
    cursor: pointer;

    @include ff(
      'font-size',
      (
        375px: 14px,
        768px: 14px,
        1200px: 20px,
      )
    );

    @media (min-width: $xl) {
      line-height: 130%;
    }

    @media (min-width: $lg) {
      margin-left: 0;
      // @include px-to-rem(17, padding-top);
      // @include px-to-rem(15, padding-bottom);
      // @include px-to-rem(20, padding-right);
      // @include px-to-rem(24, padding-left);
    }

    &:focus,
    &:hover {
      color: $primary-color;
      background-color: $yellow-color;

      & svg {
        fill: $primary-color;
        stroke: $white-color;
      }
    }

    & ~ .nav-city__list.d-block {
      background-color: $yellow-color;
    }

    & svg {
      margin-left: 0.5rem;
    }

    & span {
      white-space: nowrap;
      max-width: 120px;
      overflow: hidden;
    }
  }

  &__wrapper {
    z-index: 10;
    position: absolute;
    top: 75%;
    left: 0;

    padding: 1rem;
    padding-right: 0.2rem;
    border-radius: $borderRadius;
    background-color: $yellow-color;

    box-shadow: $boxShadowdark;
  }

  &__list {
    @include listReset();

    & li {
      min-width: 200px;
      margin-bottom: 0.25rem;
    }

    & a {
      display: block;
      font-family: $mainFont;
      text-decoration: none;
      color: $primary-color;
      transition: 0.5s;

      font-weight: 400;
      font-size: 20px;
      line-height: 130%;

      &:focus,
      &:hover {
        font-family: $mainFontBold;
        font-weight: 700;
        transition: 0.5s;
      }
    }
  }
}

.my-custom-scrollbar {
  height: 300px;
  overflow-y: auto;
  overflow-x: hidden;

  /* Scrollbar */
  &::-webkit-scrollbar {
    width: 0.45rem;
  }
  &::-webkit-scrollbar-thumb,
  &::-webkit-scrollbar-track-piece {
    background: rgba(66, 71, 77, 0.15);
    border-radius: 41px;
  }
  &::-webkit-scrollbar-track {
    background: transparent;
  }
}
