:root {
  --field-width: 100%;

  @media (min-width: $md) {
    --field-width: calc(50% - 0.5rem);
  }
}
.form {
  display: flex;
  flex-direction: column;

  &__check {
    display: flex;
    align-items: center;
    margin-top: 0.7rem;
    font-style: normal;
    font-weight: 400;
    font-size: 15px;
    line-height: 140%;
    color: #8f8f8f;
    cursor: pointer;

    @media (min-width: $xl) {
      font-size: 14px;
    }

    & span {
      padding-left: 0.6rem;

      @media (min-width: $sm) {
        padding-left: 1rem;
      }
      @media (min-width: $xl) {
        padding-left: 0.5rem;
      }
    }

    @media (min-width: $md) {
      margin-top: 1rem;
      // justify-content: end;
    }
    @media (min-width: $xl) {
      justify-content: stretch;
    }

    .form-check-input {
      width: 1.3em;
      height: 1.3em;
    }
  }

  & .form-label,
  & .form-select {
    font-family: $mainFont;
    font-style: normal;
    font-weight: 400;
    font-size: 15px;
    line-height: 140%;
    color: $primary-color;
    padding-bottom: 1.5rem;
    margin-bottom: 0;

    outline-color: $primary-color;

    @media (min-width: $xl) {
      padding-bottom: 1rem;
    }

    & span {
      display: block;
      padding-bottom: 12px;

      @media (min-width: $xl) {
        padding-bottom: 8px;
      }
    }
  }

  & .form-select,
  & .form-control {
    border-radius: 6px;
    border-color: transparent;
    padding: 1.175rem 0.75rem;
    padding-left: 1.45rem;

    @media (min-width: $xs) {
      padding: 0.775rem 1.45rem;
    }

    @media (min-width: $md) {
      padding: 0.775rem 1.25rem;
    }

    @media (min-width: $xl) {
      padding: 0.8rem 1.25rem;
      padding-left: 1.65rem;
    }

    cursor: pointer;
    &:focus,
    &:hover {
      border-color: $primary-color;
      box-shadow: none;
    }

    &::placeholder {
      font-family: $mainFont;
      font-style: normal;
      font-weight: 400;
      font-size: 20px;
      line-height: 130%;
      color: rgba(66, 71, 77, 0.5);
    }
  }
}
