.modal-tour {
  & .modal-content {
    background-color: $yellow-color;
    border-radius: $borderRadius;

    @media (min-width: $sm) {
      background-image: url(../img/modal/modal-bg-gym.jpg);
      background-repeat: no-repeat;
      background-position: 100% 0;
      background-size: 45% 100%;
    }
  }

  & .modal-dialog {
    @media (min-width: $sm) {
      max-width: 700px;
    }
  }

  & .modal-title {
    font-family: $mainFontBold;
    font-weight: 700;
    font-size: 20px;
    line-height: 130%;
  }

  & .modal-header {
    border: none;
    font-family: $mainFont;
    font-weight: 700;

    @media (min-width: $sm) {
      padding-left: 57px;
      padding-top: 50px;
    }
  }

  & .btn-close {
    @include buttonReset();
    opacity: 1;
    color: $white-color;
    background: none;

    display: flex;
    overflow: hidden;
    align-items: center;
    justify-content: center;
    width: 30px;
    height: 30px;

    @media (min-width: $sm) {
      position: absolute;
      top: 2rem;
      right: 2rem;
    }
  }

  & .modal-body {
    display: flex;
    flex-direction: column;

    @media (min-width: $sm) {
      padding-left: 57px;
      padding-bottom: 50px;
      width: 50%;
    }
  }
}
