.sale {
  position: relative;
  background-position: 50%;
  background-size: cover;
  background-color: $blue-color;

  min-height: 320px;

  overflow: hidden;

  &::before {
    position: absolute;
    z-index: 2;
    content: '';
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: linear-gradient(
      177.83deg,
      rgba(0, 0, 0, 0) 10.17%,
      rgba(0, 0, 0, 0.8) 136.24%
    );
  }

  & a {
    position: relative;
  }

  & img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
}
