.modal {
  &--bg {
    & .modal-content {
      background-color: $yellow-color;
      border-radius: $borderRadius;
      border: none;

      @media (min-width: $lg) {
        padding: 1rem;
        background-image: var(--pic);
        background-repeat: no-repeat;
        background-position: 100% 0;
        background-size: 45% 100%;
      }
    }

    & .modal-header {
      border: none;
      font-family: $mainFont;
      font-weight: 700;
    }

    & .btn-close {
      z-index: 10;
      position: absolute;
      top: 1rem;
      right: 1rem;
      @include buttonReset();
      opacity: 1;
      color: $white-color;
      background: none;
    }

    & .modal-body {
      display: flex;
      flex-direction: column;
      justify-content: center;
      width: 100%;
      padding: 1rem;
      font-family: $mainFont;

      font-weight: 400;
      font-size: 15px;
      line-height: 140%;
      color: $primary-color;

      @media (min-width: $lg) {
        width: 50%;
        justify-content: start;
        padding: 0 1rem;
      }

      & p {
        margin: 0;
        margin-bottom: 0.5rem;
      }

      & ul {
        margin: 0;
        margin-bottom: 1rem;
        padding-left: 0.6rem;
      }
    }
  }
}
