.city-list {
  @include listReset();

  @media (min-width: $md) {
    column-count: 2;
  }

  & li {
    min-width: 100px;
    margin-bottom: 4px;
  }

  & a {
    text-decoration: none;
    color: $primary-color;
  }

  & button {
    @include buttonReset();
    display: block;
    color: $city-list-color;

    font-family: $mainFont;
    font-weight: 400;
    font-size: 18px; // 15px
    line-height: 140%;

    width: 100%;
    text-align: left;

    &:focus,
    &:hover {
      font-family: $mainFontBold;
      font-weight: 700;
    }
  }
}
