.form-offers {
  display: flex;
  flex-direction: column;
  justify-content: start;
  padding: 1.5rem 0.7rem 3.3rem 0.9rem;
  background-color: $primary-color;

  border-radius: 22px;

  @media (min-width: $xs) {
    padding: 1.8rem 2rem 3rem;
  }
  @media (min-width: $xl) {
    // padding: 2rem 1.8rem 1.1rem;
    padding: 1.8rem;
  }



  &__wrapper {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;

    align-items: flex-start;

    @media (min-width: $md) {
      justify-content: space-between;
    }

    @media (min-width: $xl) {
    }
  }

  &__title {
    font-family: $mainFontBold;
    font-weight: 700;
    font-size: 17px;
    line-height: 120%;
    color: $white-color;
    margin-bottom: 1.6rem;

    @media (min-width: $sm) {
      margin-bottom: 2.6rem;
    }
    @media (min-width: $xl) {
      margin-bottom: 2rem;
    }
  }

  &__btns {
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    margin-top: auto;
    margin-bottom: auto;
    padding-right: 0;
    padding-top: 35px;
    width: var(--field-width);

    @media (min-width: $xs) {
      padding-right: 1.9rem;
      width: calc(50% - 0.5rem);
    }
    @media (min-width: $sm) {
      padding-right: 1.7rem;
    }
    @media (min-width: $md) {
      padding-right: 0;
      align-items: flex-start;
    }

    @media (min-width: $xl) {
      padding-right: 0.7rem;
    }
  }

  & .form-label {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    width: var(--field-width);

    @media (min-width: $xs) {
      width: calc(50% - 2.5rem);
      margin-right: 0.9rem;
    }
    @media (min-width: $md) {
      width: calc(50% - 0.5rem);

      margin-right: 0;
    }

    @media (min-width: $xxl) {
      width: calc(50% - 0.5rem);
    }
  }

  & .form-label span {
    color: $white-color;

    @media (min-width: $sm) {
      margin-bottom: 6px;
    }
  }

  & .form-control {
    align-items: flex-start;
  }

  & .form-select {
    margin-bottom: 0;
    min-height: 46px;

    font-family: $mainFont;
    font-style: normal;
    font-weight: 400;
    font-size: 20px;
    line-height: 130%;
    color: rgba(66, 71, 77, 0.5);
  }

  & .form-check-input:checked {
    background-color: #888888;
    border-color: #888888;
    margin: 0;
  }

  & button[type='submit'] {
    margin-top: 0;
    padding: 19px 29px;
    max-width: 100%;
    text-align: center;
    @media (min-width: $xl) {
      padding-top: 0.8rem;
      padding-bottom: 0.8rem;
    }
    @media (min-width: $xxl) {
      margin-right: auto;
      margin-left: 0;

      padding: 0.8rem 4.2rem;
    }
  }

  &--sale {

    & .form-label {

      @media (min-width: $xs) {
        width: calc(50% - 2.5rem);
        margin-right: 0.9rem;
      }
      @media (min-width: $md) {
        width: calc(25% - 0.5rem);

        margin-right: 0;
      }

      @media (min-width: $xxl) {
        width: calc(25% - 0.5rem);
      }
    }

    & .form-offers__btns {

      @media (min-width: $xs) {
        width: calc(50% - 2.5rem);
        margin-right: 0.9rem;
      }
      @media (min-width: $md) {
        width: calc(25% - 0.5rem);

        margin-right: 0;
      }

      @media (min-width: $xxl) {
        width: calc(25% - 1rem);
        align-items: flex-end;
      }
    }

    & button[type='submit'] {
      margin-left: auto;
      margin-right: 0;
    }
  }
}
