.modal.modal-city {
  background-color: transparent;

  &--list {
    background-color: rgba(66, 71, 77, 0.7);;
  }

  & .modal-dialog {
    max-width: 400px;
  }

  & .modal-content {
    padding: 1rem 0;
    font-family: $mainFont;
    background-color: $yellow-color;
  }

  & .modal-header {
    border: none;
    padding: 0 1rem;
    align-items: baseline;
  }

  & .modal-body {
    padding: 0 0.25rem 0 1rem;
    border-color: transparent;

    @media (min-width: $md) {
      padding: 0 1rem;
    }
  }

  & .modal-city-footer {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;

    & .button {
      margin: 0 0.5rem;
    }
  }

  & .modal-title {
    margin-bottom: 1rem;
    font-family: $mainFontBold;
    font-weight: 700;
    font-size: 15px;
    line-height: 140%;
    text-transform: uppercase;
    color: $primary-color;
  }

  & .btn-close {
    padding: 0;
    margin: 0;
    right: 7px;
    top: 0x;
    position: relative;
    width: 0.7em;
    height: 0.7em;
    opacity: 1;
  }
}
