.list-services {
  display: flex;
  flex-wrap: wrap;
  padding: 1rem;

  background-color: $blue-color;

  @media (min-width: $md) {
    padding: 1.5rem;
    grid-template-columns: repeat(2, 1fr);
  }
  @media (min-width: $xxl) {
    gap: 2rem;
    padding: 1.4rem 1rem 1.4rem 2.2rem;
  }

  &__item {
    margin-bottom: 20px;
    @media (min-width: $xs) {
      width: 50%;
    }
    @media (min-width: $md) {
      width: 47%;
      margin-bottom: 0;
    }
  }

  &__title {
    font-style: normal;
    font-weight: 700;
    font-size: 17px;
    line-height: 130%;
    text-transform: uppercase;

    @media (min-width: $xxl) {
      margin-bottom: 1rem;
    }

    & span {
      display: block;
    }
  }

  & ul {
    @include listReset();

    @media (min-width: $xs) {
      padding-right: 1rem;
    }
    @media (min-width: $md) {
      column-count: 2;
    }
    @media (min-width: $xl) {
      column-count: 1;
    }
  }

  & li {
    display: flex;
    flex-direction: row-reverse;
    flex-wrap: wrap;
    justify-content: start;
    align-items: center;
    margin-bottom: 5px;
    font-style: normal;
    font-weight: 400;
    font-size: 15px;
    line-height: 130%;

    @include ff(
      'font-size',
      (
        990px: 15px,
        1840px: 20px,
      )
    );

    @media (min-width: $md) {
      margin-bottom: 10px;
    }

    @media (min-width: $xxl) {
      margin-bottom: 0.4rem;
    }

    & span {
      width: calc(100% - 30px - 10px);
    }
  }

  & .icon {
    width: 30px;
    height: 30px;
    margin-right: 10px;
  }
}
