.main-sale {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-between;

  @media (min-width: $sm) {
    display: grid;
    gap: 30px;
    grid-template-columns: 1fr 1fr;

    grid-template-areas:
      'a a'
      'g g'
      'b b'
      'c c'
      'd e'
      'f f';
  }

  @media (min-width: $sm) {
    padding-top: 1.2rem;
  }

  @media (min-width: $xl) {
    padding-top: 1rem;
    grid-template-columns: 2fr 1fr 1fr;

    grid-template-areas:
      'a a a'
      'g g g'
      'b b b'
      'c d e'
      'f f f';
  }

  &__slider {
    width: 100%;
    grid-area: a;
    margin-bottom: 30px;

    @media (min-width: $sm) {
      margin-bottom: 0;
    }

    & .swiper-slide {
      @media (min-width: $sm) {
        min-height: 300px;
      }
      @media (min-width: $xl) {
        min-height: 500px;
      }

      & img {
        @media (min-width: $sm) {
          min-height: 300px;
        }
        @media (min-width: $xl) {
          min-height: 500px;
        }
      }
    }
  }
  &__form {
    width: 100%;
    grid-area: b;

    margin-bottom: 30px;

    @media (min-width: $sm) {
      margin-bottom: 0;
    }
  }
  & .list-services {
    width: 100%;
    grid-area: c;

    margin-bottom: 30px;

    @media (min-width: $sm) {
      margin-bottom: 0;
    }
  }
  & .section-kids {
    width: calc(100% / 2 - 0.5rem);
    margin-right: rem;
    grid-area: d;

    margin-bottom: 30px;

    @media (min-width: $sm) {
      margin-bottom: 0;
    }

    @media (min-width: $sm) {
      margin-right: 0;
      width: 100%;
    }
  }
  & .section-korus {
    width: calc(100% / 2 - 0.5rem);
    grid-area: e;

    margin-bottom: 30px;

    @media (min-width: $sm) {
      margin-bottom: 0;
    }

    @media (min-width: $sm) {
      width: 100%;
    }
  }
  & .content {
    width: 100%;
    grid-area: f;
  }

  & .timer {
    width: 100%;
    grid-area: g;
    margin-bottom: 30px;

    @media (min-width: $sm) {
      margin-bottom: 0;
    }
  }
}
