.button {
  @include buttonReset();

  font-family: $mainFontBold;
  font-weight: 700;
  font-size: 16px;
  line-height: 130%;
  transition: 0.5s;
  cursor: pointer;

  @include ff(
    'font-size',
    (
      375px: 16px,
      1280px: 20px,
    )
  );

  &--red {
    padding: 0.5rem 2rem;
    color: $white-color;
    border: 2px solid $red-color;
    background-color: $red-color;
    box-shadow: $boxShadowdark;
    border-radius: 16px;

    &:focus,
    &:hover {
      color: $red-color;
      background-color: transparent;
    }
  }
}
