.record {
  position: relative;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: flex-end;
  margin-bottom: 2rem;
  padding: 1.2rem 0 1.2rem;

  transition: 0.3s;

  &::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 1px;
    background-color: $primary-color;
  }

  &::after {
    content: '';
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 1px;
    background-color: $primary-color;
  }

  @media (min-width: $xs) {
    margin-top: 1rem;
    padding: 1rem 0;
  }

  @media (min-width: $sm) {
    margin-top: 0.8rem;
    margin-bottom: 1.9rem;
    padding-bottom: 1.2rem;
    padding-top: 1.5rem;
    padding-left: 0.5rem;
  }

  @media (min-width: $md) {
    // border-right-color: transparent;
    // border-left-color: transparent;
    margin-top: 0.4rem;
    margin-bottom: 4rem;
    padding: 1.8rem 0 1.3rem 0.6rem;
  }

  @media (min-width: $xl) {
    margin-top: 1rem;
  }

  &:focus,
  &:hover {
    outline: none;
    border-radius: 0;
    transition: 0.3s;
    cursor: pointer;
    & .record__title span {
      opacity: 1;
    }
  }

  &__title {
    display: flex;
    flex-direction: column;
    margin-bottom: 1.2rem;
    font-family: $mainFontBold;
    font-weight: 700;
    font-size: 27px;
    line-height: 130%;

    letter-spacing: 0.02em;
    text-transform: uppercase;

    margin: 0;
    padding-bottom: 1.7rem;

    @media (min-width: $sm) {
      padding-bottom: 0;
    }

    color: $primary-color;

    @include ff(
      'font-size',
      (
        375px: 27px,
        768px: 60px,
      )
    );

    & span {
      opacity: 0.6;
    }
  }

  & .link-text {
    @media (min-width: $sm) {
      margin-bottom: 17px;
    }
    @media (min-width: $md) {
      margin-bottom: 0;
    }
  }
}
